.header{
    background-color: #20232a;
    height: 60px;
    line-height: 60px;
    font-size: 18px;
    /* position: sticky;
    left: 0;
    right: 0;
    top: 0; */
}

.header a:hover{color: rgb(97, 218, 251);}

.logo{
    color: rgb(97, 218, 251);
    font-weight: 900;
}
.logo img{
    width: 20px;
    vertical-align: middle;
    margin-right: 10px;
}
.nav{

}

.nav a{
    display: inline-block;
    height: 100%;
    padding: 0 20px;
}

.nav ul{
    display: flex;    
    height: 60px;
}

.nav ul li{
    height: 100%;
}

.on{position: relative; }

.on::after{
    content: '';
    height: 4px;
    position: absolute;
    left: 0; 
    right: 0;
    bottom: 0;
    background-color: rgb(97, 218, 251);
}

.on a{color: rgb(97, 218, 251);}

.serch_form{

}

.serch_form input{
    background-color: transparent;
    border: none;
    font-size: 18px;
    padding: 5px;
}

.serch_form input:focus{
    outline: none;
    background-color: #333;
}

.right_menu{

}

.right_menu a{
    display: inline-block;
    font-size: 14px;
    padding: 0 10px;
}
.right_menu a:hover{color: rgb(97, 218, 251)}

.right_menu a:active{
    background-color: #373940;
    border-radius: 15px;
    margin: 15px 0;
    height: 30px;
    line-height: 30px;
}