.footer{
    background-color: #20232A;
    padding: 50px 0;
    font-size: 16px;
}

.list_wrap{
    flex-grow: 1;
    display: flex;
    flex-wrap: wrap;
    gap: 40px 0;
}

.li_wrap{
    width: 50%;
}
.li_wrap dt{
    color: #999;
    font-size: 14px;
    padding: 20px 0;
}

.li_wrap dd{
}
.li_wrap dd a{
    display: inline-block;
    padding: 8px 0;
    height: 100%;
}

.info{
    flex-grow: 1;
}

.logo{
    width: 160px;
}
.logo img{
    width: 100%;
}

.info small{
    display: block;
    color: #999;
    margin-top: 15px;
}